.Media {
    position: relative;
    max-width: 1440px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    &__backdrop {
        z-index: -10;
        position: absolute;
        top: 0;
        width: 100vw;
        height: 400px;
        &__fade {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            z-index: 1;
            pointer-events: none;
            height: 110%;
            width: 100%;
            background: linear-gradient(180deg, rgba(20,20,20,0) 0%, rgba(20,20,20,1) 100%);
        }
        &__fadeOSS {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            z-index: 1;
            pointer-events: none;
            height: 110%;
            width: 100%;
            background: linear-gradient(180deg, rgba(93,5,255,1) 0%, rgba(20,20,20,1) 100%);
        }
        &--img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            // object-position: top;
            filter: blur(5px);
        }
    }
    &__infos {
        margin-top: 44px;
        @include tabletLandscape {
            margin-top: 100px;
        } 
    }
    &__cast {
        background-color: $darkMedium;
        box-sizing: border-box;
        border: solid 1px $darkLight;
        width: 90%;
        border-radius: 8px;
        padding: 24px;
        margin-bottom: 12px;
        max-width: 1440px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__reco {
        background-color: $darkMedium;
        box-sizing: border-box;
        border: solid 1px $darkLight;
        width: 90%;
        border-radius: 8px;
        padding: 24px;
        margin-bottom: 12px;
    }
}